import { Component, OnInit } from '@angular/core';
import { RoleHttpService } from './services/role-http.service';


import { Subscription } from 'rxjs';
import { ButtonOptionsI } from '@shared/models/titlebar.model';
import { UserI } from '../users/models/users.model';
import { API_CONFIG} from '@const/api.constant';
// import { SpinnerService } from '@shared/services/spinner.service';
// import { ToastService } from '@shared/services/toast.service';

@Component({
  selector: 'app-role',
  templateUrl: './role.component.html',
  styleUrls: ['./role.component.scss']
})
export class RoleComponent implements OnInit {

  readonly button: ButtonOptionsI = {
    label: 'Add New Role',
    routerLink: ['/role/add']
  };
  readonly pageTitle = 'Role';
  readonly pageSize = API_CONFIG.pageSize;
  paginationTotalCount = 0;
  currentPage = 1;
  where: { [key: string]: any } = {};
  roleCount:number = 0;
  roleList: UserI[] = [];
  roleFilter: { [key: string]: any } = {};
  subscriptions: Subscription = new Subscription();
  constructor(
    private readonly _rolehttps:RoleHttpService,
  ) { }

  ngOnInit(): void {
    this.getUserList();
  }
  getUserList() {
    this.subscriptions.add(
      this._rolehttps.fetchAllRole(this.pageSize,this.currentPage).subscribe(resp=>{
        this.roleList = resp.data
         this.roleCount = resp.count
      })
    )  
  }
  onPageChange(currentPage: number) {
    this.currentPage = currentPage;
    this.getUserList();
  }
  onStatusUpdate(role){
    this.subscriptions.add(
      this._rolehttps.deleteEndpoint(role).subscribe(
        (resp: any) => {
          this.getUserList();
        },
        (err) => {
         
        }
      )
    );
  }

}
