import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

// Rxjs
import { Subscription } from 'rxjs';
import { RoleI} from '../models/role.model';
import { RoleHttpService } from '../services/role-http.service'
import { StatusEnum } from '@const/api.constant';

@Component({
  selector: 'app-edit-role',
  templateUrl: './edit-role.component.html',
  styleUrls: ['./edit-role.component.scss']
})
export class EditRoleComponent implements OnInit {

  roleWrapper: RoleI;
  roleID:string;
  private subscriptions: Subscription = new Subscription();

  constructor(
    private _router: Router,
    private _route: ActivatedRoute,
    private _roleHttp: RoleHttpService
  ) {
    this.roleID = this._route.snapshot.params.id;
  }

  ngOnInit() {
    this.initiateUrlForm();
    this.roleGetByID();
  }

  initiateUrlForm() {
    this.roleWrapper = {
      name: '',
      statusID: StatusEnum.ACTIVE
    };
  }
  roleGetByID(){
    this.subscriptions.add(
      this._roleHttp.fetchRoleById(this.roleID)
      .subscribe(
        (resp) => {
          console.log(resp);
          
        // this.roleWrapper = resp['responseData'];
        // this._spinner.hide();
        },
        err => {
          // this._spinner.hide();
          // this._toast.error(err.error.message, 'Mail Template');
        }
      )
    );
  }
  onRoleSubmit(){
    
  }

  redirectToEndpointListing() {
    this._router.navigate(['/role']);
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
    // this._spinner.hide();
  }

}
